<template>
    <div id="app">
        <div id="chat-area">
            <div class="messages-area" :style="{ 'overflow-y': showInput ? 'auto' : 'hidden' }" v-if="chatOpened"
                ref="messagesRef">
                <div class="message" :key="key" v-for="(msg, key) in filteredMessages">
                    <span class="message-type">{{ msg.type }}</span>
                    <div class="message-content">
                        <span class="message-sendedBy">{{ msg.author }}:</span>
                        {{ msg.content }}
                    </div>
                </div>
            </div>
            <div id="input-area" v-if="showInput">
                <div class="input-box">
                    <div class="commandPrompt">
                        <input type="text" maxlength="250" spellcheck="false" class="input" v-model="message" autofocus
                            @keypress.enter.prevent="send" ref="input" placeholder="Digite sua mensagem aqui...">
                    </div>
                    <img src="./assets/images/emoji.svg" :style="{ 'opacity': emojiPickerOpened ? '1' : '.5' }"
                        @click="emojiPickerOpened = !emojiPickerOpened" class="emoji" />
                    <img src="./assets/images/send.svg" @click="send" class="send" />
                </div>
                <div class="notify-box" @click="changeNotify">
                    <img :style="{ width: notifyToggled ? '0.833vw' : '1.146vw' }"
                        :src="notifyToggled ? require('./assets/images/notify_on.svg') : require('./assets/images/notify_off.svg')">
                </div>
            </div>
            <div class="filter-types-area" v-if="showInput">
                <button class="chatTypeBackground" @click="() => changeChatType('all')"
                    :class="{ 'selected': selectedChatType === 'all' }">Todos</button>
                <button class="chatTypeBackground" @click="() => changeChatType('chat')"
                    :class="{ 'selected': selectedChatType === 'chat' }">CHAT</button>
                <button class="chatTypeBackground" v-for="(type, key) in chatTypes" :key="key"
                    @click="() => changeChatType(type)"
                    :class="{ 'selected': selectedChatType === type }">{{ type }}</button>
                <div class="emoji-area" v-show="emojiPickerOpened">
                    <div class="emoji-item" @click="() => selectEmoji(emoji)" :key="k" v-for="(emoji, k) in emojiList">
                        {{ emoji }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import emojis from './emojis.json';
import post from './utils/post';

export default {
    data() {
        return {
            emojiPickerOpened: false,
            emojiList: emojis[1].split(' '),
            blockedMessages: [],
            chatOpened: false,
            showInput: false,
            chatTypes: [],
            selectedChatType: 'all',
            listener: null,
            input: null,
            notifyToggled: true,
            message: '',
            messages: [],
            messagesRef: null,
            cachedMessages: [],
            currCachedMsg: 1,
        }
    },

    watch: {

        messages: {
            handler() {
                if (this.showChatTimer) {
                    clearTimeout(this.showChatTimer);
                }

                if (this.notifyToggled) {
                    this.chatOpened = true;
                    const messagesObj = this.$refs.messagesRef;
                    if (this.chatOpened) {
                        this.$nextTick(() => {
                            messagesObj.scrollTop = messagesObj.scrollHeight;
                        });
                    }
                }
                this.resetShowChatTimer();

            },
            deep: true
        },
    },

    computed: {
        filteredMessages() {
            return this.selectedChatType !== 'all' ? this.messages.filter(el => el.type === this.selectedChatType) : this.messages;
        }
    },

    methods: {
        changeChatType(type) {
            this.selectedChatType = type;
            const messagesObj = this.$refs.messagesRef;
            this.$nextTick(() => {
                messagesObj.scrollTop = messagesObj.scrollHeight;
            });
        },

        isCommand() {
            if (this.message[0] === '/') return true;
            return false;
        },

        changeNotify() {
            this.notifyToggled = !this.notifyToggled
        },

        checkBlockedMessages() {
            let hasBlocked = false;
            this.message.split(' ').map(v => {
                this.blockedMessages.forEach((v) => {
                    if (this.message.includes(v)) {
                        hasBlocked = true;
                        return;
                    }
                });
                if (hasBlocked) return;
            })

            return !hasBlocked;
        },

        send() {
            if (this.message !== '') {
                if (this.checkBlockedMessages()) {
                    if (this.cachedMessages.length > 5) this.cachedMessages.shift();
                    this.cachedMessages.push(this.message);
                    post('ChatSubmit', { message: this.message, tag: this.selectedChatType == 'all' ? 'chat' : this.selectedChatType });

                    this.message = '';
                    this.hideInput();
                }
            } else {
                this.hideInput();
            }
        },

        keyUp() {
            if ((this.currCachedMsg > 5) || this.currCachedMsg > this.cachedMessages.length) return;
            this.message = this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
            this.currCachedMsg++;
        },

        keyDown() {
            if (this.currCachedMsg <= 1) return;
            this.currCachedMsg--;
            this.message = this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
        },

        clear() {
            this.messages = [];
        },

        selectEmoji(emoji) {
            this.message += emoji;
            this.emojiPickerOpened = false;
        },

        clearShowChatTimer() {
            clearTimeout(this.showChatTimer);
        },

        resetShowChatTimer() {
            this.clearShowChatTimer();
            this.showChatTimer = setTimeout(() => {
                if (!this.showInput) {
                    this.chatOpened = false;
                }
            }, 7000);
        },

        hideInput() {
            this.message = '';
            this.showInput = false;
            this.resetShowChatTimer();
            post('close');
        },
    },

    destroyed() {
        clearInterval(this.focusTimer);
        window.removeEventListener('message', this.listener);
    },

    mounted() {
        this.listener = window.addEventListener('message', (event) => {
            const item = event.data || event.detail;
            switch (item.Action) {
                case "Chat":
                    this.chatOpened = true;
                    this.showInput = true;
                    this.currCachedMsg = 1;
                    this.chatTypes = item.Data;
                    this.blockedMessages = item.Block;
                    setTimeout(() => {
                        this.$nextTick(() => {
                            this.$refs.input.focus();
                            this.$refs.messagesRef.scrollTop = this.$refs.messagesRef.scrollHeight;
                        });
                    }, 125);
                    break;

                case "Message":
                    this.messages.push({
                        type: item.Type,
                        author: item.Author,
                        content: item.Message
                    });
                    break;

                default:
                    break;
            }
        });

        window.addEventListener('keydown', event => {
            if (event.code == "Escape") {
                this.hideInput();
            } else if (event.code == "ArrowUp") this.keyUp();
            else if (event.code == "ArrowDown") this.keyDown();

        })
    }
}
</script>

<style scoped>
#app {
    width: 100vw;
    height: 100vh;
}

#chat-area {
    position: absolute;
    top: 2.031vw;
    left: 1.771vw;
    display: flex;
    flex-direction: column;
    width: 25.573vw;
}

.messages-area {
    display: flex;
    flex-direction: column;
    gap: 0.26vw;
    max-height: 11.563vw;
    overflow-y: auto;
    direction: rtl;
    padding-left: 0.417vw;
    width: 100%;
    overflow-x: hidden;
}

.message {
    width: 100%;
    display: flex;
    align-items: flex-start;
    height: 100%;
    direction: ltr;
}

.message-type {
    background: rgba(0, 0, 0, 0.5);
    border-top-left-radius: 0.208vw;
    border-bottom-left-radius: 0.208vw;
    font-weight: 500;
    color: #fff;
    font-size: 0.625vw;
    padding: 0.313vw 0.729vw;
    text-transform: uppercase;
    height: 100%;
    letter-spacing: 0.031vw;
}

.message-content {
    width: max-content;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.2) 100%);
    border-top-right-radius: 0.208vw;
    border-bottom-right-radius: 0.208vw;
    font-weight: 300;
    font-size: 0.625vw;
    color: rgba(255, 255, 255, 0.7);
    padding: 0.313vw 0.729vw;
    letter-spacing: 0.031vw;
}

.message-sendedBy {
    color: rgba(255, 255, 255, 0.4);
}

#input-area {
    display: flex;
    gap: 0.313vw;
    margin-top: 1.094vw;
}

.input-box {
    width: 23.177vw;
    height: 2.083vw;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 0.208vw;
    padding: 0 0.729vw;
    display: flex;
    gap: 1.302vw;
}

.input {
    flex: 1;
    margin-left: 0.223vw;
    background: none;
    outline: none;
    border: none;
    font-weight: 500;
    font-size: 0.625vw;
    color: #ebebeb;
}

.input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.emoji {
    width: 0.938vw;
    cursor: pointer;
}

.send {
    width: 0.677vw;
    cursor: pointer;
}

.notify-box {
    background: rgba(0, 0, 0, 0.5);
    border-radius: 0.208vw;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.083vw;
    cursor: pointer;
}

.notify-box img {
    width: 0.833vw;
}

.filter-types-area {
    display: flex;
    margin-top: 0.677vw;
    gap: 0.417vw;
}

.filter-types-area button {
    padding: 0.313vw 0.417vw;
    color: #fff;
    font-weight: 500;
    font-size: 0.625vw;
    line-height: 0.938vw;
    border-radius: 0.208vw;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    height: max-content;

}

.emoji-area {
    position: absolute;
    flex: 1;
    right: 0;
    width: 15vw;
    height: 9.688vw;
    padding: 0.417vw;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 0.208vw;
    display: flex;
    gap: 0.417vw;
    flex-wrap: wrap;
    overflow: auto;
    transition: all .2s ease-in-out;
}

.emoji-item {
    cursor: pointer;
    opacity: .5;
    transition: .2s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.emoji-item:hover {
    opacity: 1;
    transform: scale(1.05);
}

.commandPrompt {
    align-items: center;
    display: flex;
    flex: 1;
    border-radius: 0.208vw;
}

.commandText {
    font-size: 0.625vw;
    color: #fff;
    font-weight: 600;
    padding: 0.208vw;
    border-radius: 0.208vw;
    background-color: rgba(255, 255, 255, .08);
    margin-left: 0.573vw;
}

.chatTypeBackground {
    text-transform: uppercase;
    background: rgba(255, 255, 255, 0.3);
    opacity: .4;
    letter-spacing: 0.031vw;
}

.selected {
    background: #980804;
    opacity: 1;
}
</style>
