export default async (event, data, mockData) => {
    const options = {
        method: 'POST',
        headers: {
            'Content-type': 'application/json; charset=UTF-8',
        },
        body: JSON.stringify(data || {})
    }

    const resourceName = window.GetParentResourceName ? window.GetParentResourceName() : 'santa_chat';
    
    const res = await fetch(`https://${resourceName}/${event}`, options);
    return await res.json();
}

